  align-self: end;
}
.feedback-form__item--agreement label {
  position: relative;
}
@media (max-width: 1023px) {
  .feedback-form__item--btn {
    margin-right: 55px;
    padding-top: 18px;
    padding-bottom: 11px;
  }
  .feedback-form__item--agreement {
    padding-left: 25px;
    margin-bottom: 4px;
  }
}
@media (max-width: 767px) {
  .feedback-form__item--btn {
    margin: 0;
    margin-top: 5px;
    padding-top: 15px;
  }
  .feedback-form__item--agreement {
    margin: 0;
    margin-top: 5px;
    padding-left: 30px;
