}
.feedback-form input:focus,
.feedback-form textarea:focus {
  outline: 1px solid #ffffff;
}

input:checked + .feedback-form__agreement-mark {
  background: url("../img/sprite/icon-checkbox.svg");
}

input:hover + .feedback-form__agreement-mark {
  background-color: #026593;
}

input:focus + .feedback-form__agreement-mark {
  outline: 1px solid #ffffff;
}

.feedback-form__agreement-mark {
  position: absolute;
  width: 15px;
  height: 15px;
  left: -25px;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.feedback-form textarea {
  padding-top: 25px;
  min-height: 155px;
}
@media (max-width: 1023px) {
  .feedback-form textarea {
    padding-top: 34px;
    padding-bottom: 10px;
  }
}
@media (max-width: 767px) {
  .feedback-form textarea {
    padding-top: 28px;
    min-height: 182px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 144px 25px 83px 10px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 500px;
  padding: 40px;
  padding-right: 30px;
  border-radius: 3px;
  background-color: #00b2ff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 18px;
  right: 16px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__form-group {
  grid-template-columns: 1fr;
  margin-left: 0;
  row-gap: 22px;
}

.modal__form h3 {
  font-size: 30px;
  line-height: 42px;
  margin-bottom: 4px;
}

.modal__form p {
  margin-left: 0;
  margin-bottom: 36px;
}

.modal__form-item--btn {
  margin-top: -3px;
  margin-right: 0;
  padding-bottom: 10px;
}
.modal__form-item--agreement {
  padding-left: 33px;
}

@media (max-width: 767px) {
  .accordion .accordion {
    width: 90%;
    margin: 0 auto;
  }
  .accordion__element {
    border-top: 1px solid rgba(248, 253, 255, 0.7);
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
  .accordion__element:first-child {
    border-bottom: none;
  }
  .accordion__element.is-active > .accordion__btn::before {
    display: none;
  }
  .accordion__btn {
    margin: 0;
    padding: 19px 25px 18px;
    position: relative;
    text-transform: uppercase;
    cursor: pointer;
  }
  .accordion__btn::after {
    content: "";
    position: absolute;
    right: 27px;
    top: 27px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
  }
  .accordion__btn::before {
    content: "";
    position: absolute;
    right: 27px;
    top: 27px;
    width: 15px;
    height: 1px;
    background-color: #f8fdff;
    transform: rotate(90deg);
  }
  .accordion__btn:active {
    background-color: #00b2ff;
  }
  .accordion__content {
    padding: 0 25px;
  }
  .accordion__content li:first-child {
    margin-top: -4px;
  }
  .accordion__content li:last-child {
    margin-bottom: 15px;
  }
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

